.body {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch-center {
  background-color: #f6a603;
  border-radius: 50%;
  justify-content: center;
  width: 5vmin;
  height: 5vmin;
  display: flex;
  position: absolute;
  top: calc(50% - 2.5vmin);
  left: calc(50% - 2.5vmin);
}

.stopwatch__stopwatch-center--minute:before {
  content: "";
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  position: absolute;
  top: 50%;
  transform: rotate(0);
}

.stopwatch__stopwatch-center--second:before {
  content: "";
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  position: absolute;
  top: 50%;
}

.stopwatch--speed-up--10 .stopwatch__stopwatch-center--minute {
  animation: 600s steps(60, end) infinite move;
}

.stopwatch--speed-up--10 .stopwatch__stopwatch-center--second {
  animation: 10s linear infinite move;
}

.stopwatch--speed-up--60 .stopwatch__stopwatch-center--minute {
  animation: 3600s steps(60, end) infinite move;
}

.stopwatch--speed-up--60 .stopwatch__stopwatch-center--second {
  animation: 60s linear infinite move;
}

@keyframes move {
  0% {
    transform: rotate(180deg);
  }

  100% {
    transform: rotate(540deg);
  }
}

